// src/data/loadNations.ts
import { NationData, TraitOrFlaw } from "../types";

// Magic Webpack function that loads all files matching the pattern
// @ts-ignore
const nationJsons = require.context("../data/nations", true, /nation\.json$/);

// @ts-ignore
const traitsJsons = require.context("../data/nations", true, /traits\.json$/);
// @ts-ignore
const flawsJsons = require.context("../data/nations", true, /flaws\.json$/);

//console.log("nationJsons.keys():", nationJsons.keys());

export function loadAllNationData(): NationData[] {
  // Get all unique folders
  const folders = nationJsons.keys().map((path: string) =>
    // "./America/nations.json" → "America"
    path.split("/")[1]
  );

  return folders.map((folder: string) => {
    // Safe read, fallback to []
    let nationObj: any = {};
    let traits: TraitOrFlaw[] = [];
    let flaws: TraitOrFlaw[] = [];
    try {
      nationObj = nationJsons(`./${folder}/nations.json`);
    } catch {}
    try {
      traits = traitsJsons(`./${folder}/traits.json`);
    } catch {}
    try {
      flaws = flawsJsons(`./${folder}/flaws.json`);
    } catch {}
    return {
      nation: nationObj.nation ?? folder,
      traits: traits ?? [],
      flaws: flaws ?? [],
      // Add other nation fields if you expand nations.json later
    };
  });
}
